/* App.css */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.post {
  margin-bottom: 20px;
  padding: 15px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
}

.post h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.post p {
  font-size: 1rem;
  line-height: 1.6;
}

.post-date {
  font-size: 0.9rem;
  color: #666;
}

.post-img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}
